import { multiAlign, sampleAlign } from "@/js/alignment"
import { groupScratch } from "@/js/rendering"
import { Sample } from "@/js/sampletable"

const webWorker: Worker = self as any

onmessage = function(msg) {
    const process = msg.data.process
    switch(process) {
        case 'multiAlign': {
            const multi_result = multiAlign(msg.data.data)
            // console.log('msg.data.data', msg.data.data[0].sample.group, msg.data.data)
            // console.log('multi_result', multi_result[0].sample.group, multi_result)
            webWorker.postMessage({process: 'multiAlign', data: multi_result})
            break
        }
        case 'chartData': {
            const data = groupScratch(msg.data.data)
            webWorker.postMessage({process: 'chartData', data: data})
            break
        }
        case 'groupAlign': {
            const sample_in_group = msg.data.data
            const genef = msg.data.genef
            const alignment_result_in_sample = []
            for (const sample of sample_in_group) {
                // web worker variable type adjust
                const sampleinfo = new Sample(sample.sample.name, sample.sample.gene, sample.sample.gRNA_PAM, sample.sample.barcode_L, sample.sample.barcode_R, sample.sample.group)
                sampleinfo.sumup = sample.sample.sumup
                const alignment_result = sampleAlign({sample: sampleinfo, haplotype: sample.haplotype}, genef)
                alignment_result_in_sample.push(alignment_result)
            }
            webWorker.postMessage({process: 'groupAlign', data: alignment_result_in_sample})
        }
    }
}

export default null as any