import { jbfilereadersync } from "./filereader"

export class Fasta {
    file: File
    filereader: any
    gzipped: boolean
    // fasta2Dna class
    constructor(file: File) {
        this.file = file
        this.gzipped = this.file.name.match(/(\.gz)$/) ? true: false
        this.filereader = new jbfilereadersync(this.file, this.gzipped)
    }

    /*
    
    for (const dna of fasta) {
        //pass
    }
    */
    * [Symbol.iterator]() {
        let counter = 0
        const rec: {[key: number]: string} = {0: '', 1: ''}
        while (!this.filereader.eof) {
            const line = this.filereader.readline()
            if (line != '\n') {
                if (counter < 1) {
                    rec[counter] = line.trim()
                    counter += 1
                } else if (counter === 1) {
                    rec[counter] = line.trim().toUpperCase()
                    counter = 0
                    yield new Dna(rec[0], rec[1])
                }
            }
        }
    }
}

export class Fastq {
    file: File
    filereader: any
    gzipped: boolean
    strand: string
    readingPgCallback: (p: number, s: string) => void
    // fastq4Dna
    constructor(file: File, isForward = true, readingPgCallback: (p: number, s: string) => void) {
        this.file = file
        this.gzipped = this.file.name.match(/(\.gz)$/) ? true: false
        this.filereader = new jbfilereadersync(this.file, this.gzipped)
        this.strand = isForward ? 'forward': 'reverse'
        this.readingPgCallback = readingPgCallback
    }

    * [Symbol.iterator]() {
        let counter = 0
        const rec: {[key: number]: string} = {0: '', 1: '', 2: '', 3: ''}
        let prepercent = 0
        while (!this.filereader.eof) {
            const line = this.filereader.readline()
            const percentage = this.filereader.fpos * 100 / this.file.size
            if (line != '\n') {
                if (counter < 3) {
                    rec[counter] = line.trim()
                    counter += 1
                } else if (counter === 3) {
                    rec[counter] = line.trim()
                    counter = 0
                    yield new Dna(rec[0], rec[1], rec[3])
                }
            }
            if (percentage - prepercent > 1) {
                this.readingPgCallback(percentage, this.strand)
                prepercent = percentage
            }
        }
        this.readingPgCallback(100, this.strand)
    }
}

export class Dna {
    header: string
    seq: string
    qual: string | undefined
    type: string
    seqArray: string[]
    /**
     * One read of fasta
     * @param {string} header sequence name
     * @param {string} sequence sequence
     * @param {string} quality quality of sequence
     */
    constructor(header: string, sequence: string, quality?: string) {
        this.header = header.replace(/^@+|\n$|^>+/g, '')
        this.seq = sequence
        this.seqArray = sequence.split("")
        this.qual = quality
        if (quality) {
            this.type = 'fastq'
        } else {
            this.type = 'fasta'
        }
    }

    /**
     * 
     * @returns sequence string
     */
    stringify() {
        //string
        if (this.type === 'fasta') {
            return `>${this.header}\n${this.seq}\n`
        } else if (this.type === 'fastq'){
            return `@${this.header}\n${this.seq}\n+${this.header}\n${this.qual}`
        }
    }

    length() {
        //sequence length
        return this.seq.length
    }

    complement(a: string) {
        return ({ A: 'T', T: 'A', G: 'C', C: 'G' , R: 'Y', Y: 'R', a: 't', t: 'a', c: 'g', g: 'c',r: 'y', y: 'r'} as { [key:string]: string })[a]
    }

    revcomp() {
        //
        return this.seq.split('').reverse().map(this.complement).join('')
    }

    rqual() {
        //quality
        if (this.qual) {
            return this.qual.split('').reverse().join('')
        } else {
            return 'N'
        }
    }
}