import { Fasta } from './fasta'
import bioseq from 'bioseq'
import { notify } from "@kyvg/vue3-notification"
import { Sample } from './sampletable'

/**
 * align haplotypes in sample
 * @param hap_by_sample 
 * @param gene_file 
 * @returns {sample: sampleinfo, haplotype: alignment result[]}
 */
function sampleAlign(hap_by_sample: {sample: Sample, haplotype: {[key: string]: number}}, gene_file: File) {
    const res = {sample: hap_by_sample.sample, haplotype: [] as {result: any, count: number}[]}
    const gene_fasta = new Fasta(gene_file)
    for (const gene of gene_fasta) {
        if (gene.header == hap_by_sample.sample.gene) {
            hap_by_sample.sample.getRef(gene.seq)
            for (const raw_query in hap_by_sample.haplotype) {
                const query = hap_by_sample.sample.reverse ? revcomp(raw_query): raw_query
                const rst = bioseq.align(hap_by_sample.sample.ref, query, false)
                res.haplotype.push({result: bioseq.cigar2gaps(hap_by_sample.sample.ref, query, rst.position, rst.CIGAR), count: hap_by_sample.haplotype[raw_query]})
            }
        }
    }
    return res
}

/**
 * align all haplotype by samples
 * @param genes 
 * @param genefile 
 * @param alignmentPgCallback show progress on page
 * @param samplecount 
 * @returns {string: {string: string | string: {string: number | string: string[]}[]}} {genename: {note: string, vector: string, result: {count: number, result:[reference seq: string, query seq: string]}[]}}
 */
function batchAlignment(samples: {sample: Sample, haplotype: {[key: string]: number}, sumup: number}[], genefile: File, alignmentPgCallback: (p: number) => void) {
    const res: {[key: string]: {[key: string]: any}} = {}
    let percentage = 0
    const genesfasta = new Fasta(genefile)
    const starttime: number = new Date().getTime()
    for (const gene of genesfasta) {
        let geneseq = gene.seq
        for (const sample of samples) {
            if (gene.header == sample.sample.gene) {
                const gRNA_PAM = (sample.sample.gRNA_PAM as string)
                for (let i=0;i<2;i++) {
                    if (geneseq.indexOf(gRNA_PAM) == -1) {
                        geneseq = revcomp(gene.seq)
                        if (i==1) {
                            notify({
                                title: "Alert",
                                text: 'gRNA_PAM ' + gRNA_PAM + ' not found in gene ' + sample.sample.gene + ' sequence!'
                            })
                        }
                    }
                }
                //search sequence for pam
                const gRNA_PAM_pos = geneseq.indexOf(gRNA_PAM)
                const reference = geneseq.slice(gRNA_PAM_pos - 70 > 0 ? gRNA_PAM_pos - 70 : 0, gRNA_PAM_pos + 70 + gRNA_PAM.length)
                if (!res[sample.sample.gene]) {
                    res[sample.sample.gene] = {[sample.sample.group]: {sample: {[sample.sample.name]: {result: [], sumup: sample.sumup, ref: reference, gRNA_PAM: sample.sample.gRNA_PAM}}}}
                } else if(!res[sample.sample.gene][sample.sample.group]) {
                    res[sample.sample.gene][sample.sample.group] = {sample: {[sample.sample.name]: {result: [], sumup: sample.sumup, ref: reference, gRNA_PAM: sample.sample.gRNA_PAM}}}
                } else {
                    res[sample.sample.gene][sample.sample.group]['sample'][sample.sample.name] = {result: [], sumup: sample.sumup, ref: reference, gRNA_PAM: sample.sample.gRNA_PAM}
                }
                for (const contig in sample.haplotype) {
                    let haplotype = contig
                    if (contig.indexOf(gRNA_PAM) == -1) {
                        haplotype = revcomp(contig)
                    }
                    const query = haplotype
                    const rst = bioseq.align(reference, query, false)
                    res[sample.sample.gene][sample.sample.group]['sample'][sample.sample.name].result.push({result: bioseq.cigar2gaps(reference, query, rst.position, rst.CIGAR), count: sample.haplotype[contig]})
                }
                percentage += 100 / (samples.length)
                alignmentPgCallback(percentage)
            }
        }
    }
    const duration: number = new Date().getTime() - starttime
    console.log(`Alignment completed for ${samples.length} samples in ${duration/1000} seconds`)
    alignmentPgCallback(100)
    return res
}

function revcomp(seq: string) {
    return seq.split('')
        .reverse()
        .map(a => ({ A: 'T', T: 'A', G: 'C', C: 'G' , a: 't', t: 'a', c: 'g', g: 'c'} as { [key:string]: string })[a])
        .join('')
}

function getIndices(searchRegExp: RegExp, str: string) {
    const indices = []
    let match
    while ((match = searchRegExp.exec(str)) !== null) {
        let length_sum = 0
        for (const indice of indices) {
            length_sum += indice.length
        }
      indices.push({index: match.index - length_sum, length: match[0].length})
    }
    return indices
}


class AlignedPair {
    reference: string
    query: string
    gRNA_PAM: string
    clean_reference: string
    gRNA_PAM_pos: number
    constructor(reference: string, query: string, gRNA_PAM: string) {
        this.reference = reference
        this.query = query
        this.gRNA_PAM = gRNA_PAM
        this.clean_reference = this.reference.replace(/(A|T|C|G|a|t|c|g)-+(?=(A|T|C|G|a|t|c|g))/g, '$1')
        this.gRNA_PAM_pos = this.clean_reference.indexOf(this.gRNA_PAM)
    }

    multi_result():{query: string, insertions:{index: number, sequence: string}[]} {
        const result: {query: string, insertions:{index: number, sequence: string}[]} = {query: '', insertions: []}
        result.query = this.remove_insertion().slice(this.gRNA_PAM_pos - 30, this.gRNA_PAM_pos + this.gRNA_PAM.length + 30)
        const insertions = this.get_insertion()
        let i = 0
        while (i<insertions.length) {
            insertions[i].index -= (this.gRNA_PAM_pos - 30)
            // console.log(`${insertions[i].index} > ${this.gRNA_PAM.length+60} is ${insertions[i].index > this.gRNA_PAM.length + 60}`)
            if (insertions[i].index < 0 || insertions[i].index > this.gRNA_PAM.length + 60) {
                insertions.splice(i, 1)
            } else {
                i++
            }
        }
        result.insertions = insertions
        return result
    }

    remove_insertion():string {
        const indices = getIndices(/(A|T|C|G|a|t|c|g)-+(?=(A|T|C|G|a|t|c|g))/g, this.reference)
        const new_indices = this.lookbehindAdjust(indices)
        const arrayQuery = this.query.split('')
        for (const insertion of new_indices) {
            arrayQuery.splice(insertion.index, insertion.length)
        }
        return arrayQuery.join('')
    }

    get_insertion():{index: number, sequence: string}[] {
        const indices = getIndices(/(A|T|C|G|a|t|c|g)-+(?=(A|T|C|G|a|t|c|g))/g, this.reference)
        const new_indices = this.lookbehindAdjust(indices)
        const arrayQuery = this.query.split('')
        const insertions: {index: number, sequence: string}[] = []
        for (const insertion of new_indices) {
            const insert = arrayQuery.splice(insertion.index, insertion.length)
            insertions.push({index: insertion.index, sequence: insert.join('')})
        }
        return insertions
    }

    lookbehindAdjust(indices: {index: number, length: number}[]):{index: number, length: number}[] {
      for (const indice of indices) {
        indice.index += 1
        indice.length -= 1
      }
      return indices
    }
}

function multiAlign(groupres: {sample: Sample, haplotype: {result: any, count: number}[]}[]):{sample: Sample, res: {query: string, count: number, insertions:{index: number, sequence: string}[]}[]}[] {
    const resultset: {sample: Sample, res: {query: string, count: number, insertions:{index: number, sequence: string}[]}[]}[] = []
    for (const sample of groupres) {
        // init
        let ref = sample.sample.ref as string
        const result: {sample: Sample, res: {query: string, count: number, insertions:{index: number, sequence: string}[]}[]} = {sample: sample.sample, res: []}
        const gRNA_PAM = sample.sample.gRNA_PAM
        sample.sample.ref = ref.slice(ref.indexOf(gRNA_PAM) - 30, ref.indexOf(gRNA_PAM)+gRNA_PAM.length+30)
        ref = sample.sample.ref
        for (const res of sample.haplotype) {
            const pair = new AlignedPair(res.result[0], res.result[1], gRNA_PAM)
            const multi_res = pair.multi_result()
            result.res.push({query: multi_res.query, count: res.count, insertions: multi_res.insertions})
        }
        // lowercase mutant loci & filter over-replaced haplotype
        for (const re of result.res) {
            let replacement_count = 0
            let space_count = 0
            for (let i=0;i<ref.length-1;i++) {
              if (re.query[i] === '-') {
                space_count += 1
              } else if (re.query[i] !== ref[i] && re.query[i] !== '-' && ref[i] !== '-') {
                re.query = re.query.slice(0,i) + re.query[i].toLowerCase() + re.query.slice(i+1)
                replacement_count += 1
              }
            }
            if (ref.length - space_count < replacement_count * 2 || replacement_count < 5) result.res.splice(result.res.indexOf(re), 1)
        }
        // remove duplicates
        result.res = result.res.map(line => {
            let new_count = line.count
            if (line.insertions.length === 0) {
                for (const line1 of result.res) {
                    if (line.query === line1.query && line1.insertions.length === 0) new_count += line1.count
                }
            }
            if (new_count !== line.count) new_count -= line.count
            return {query: line.query, count: new_count, insertions: line.insertions}
        }).filter((ele, index, array) => index === array.findIndex(e => JSON.stringify(e) === JSON.stringify(ele)))
        // sort by count
        result.res.sort((a, b) => a.count < b.count ? 1 : a.count > b.count ? -1 : 0)
        resultset.push(result)
    }
    return resultset
}

export {
    sampleAlign,
    batchAlignment,
    AlignedPair,
    multiAlign
}