import { jbfilereadersync } from "./filereader"
import xlsx from 'xlsx'
import { notify } from "@kyvg/vue3-notification"


/**
 * python zip function works only for arrays
 * @param rows [a, b, c] [d, e, f]
 * @returns [a, d], [b, e], [c, f]
 */
const zip = (...rows: any[]) => [...rows[0]].map((_,c) => rows.map(row => row[c]))
const azip = (rows: any) => rows[0].map((_: any,c: any) => rows.map((row: any) => row[c]))

/**
 * read information from tables
 */
class Table {
    file: File
    format: string | undefined
    filereader: any
    withHeader: boolean
    delimiter: string | undefined
    header: string[]
    table: {[key: string]: string}[]
    sampletable: {[key: string]: {[key: string]: number}} | undefined
    gene_sample_table: {[key: string]: {[key: string]: {[key: string]: string | {[key: string]: number}}}} | undefined
    /**
     * 
     * @param file table file
     * @param withHeader if table has header, default is true
     * @param header table header, default is ['Index', 'Sample', 'Barcode_L', 'Barcode_R']
     */
    constructor(file: File, withHeader = true, header = ['Index', 'Sample', 'Barcode_L', 'Barcode_R']) {
        // todo: 
        if (file.name.match(/\.tsv$/) || file.name.match(/\.txt$/)) {
            this.format = 'tsv'
            this.delimiter = '\t'
        } else if (file.name.match(/\.csv$/)) {
            this.format = 'csv'
            this.delimiter = ','
        } else if (file.name.match(/\.xlsx$/)) {
            this.format = 'xlsx'
        } else {
            notify({
                title: "Alert",
                text: "unrecognized file format."
              })
        }
        this.file = file
        this.header = header
        this.filereader = new jbfilereadersync(file, false)
        this.withHeader = withHeader
        this.table = this.read()
    }
    
    read() {
        let table: {[key: string]: string}[] = []
        if (this.withHeader) {
            this.header = this.filereader.readline().split(this.delimiter)
        }

        if (this.delimiter) {
            while (!this.filereader.eof) {
                const record = this.filereader.readline().split(this.delimiter)
                if (record.length > 1) {
                    const line: {[key: string]: string} = {}
                    for (const [fieldname, field] of zip(this.header, record)) {
                        line[fieldname] = field
                    }
                    if (Object.keys(line).length == this.header.length) table.push(line)
                }
            }
        } else {
            table = this.readWorkbookFromLocalFile(this.file, this.readWorkbook)
        }
        return table
    }

    readWorkbookFromLocalFile(file: File, callback: (workbook: xlsx.WorkBook) => {[key: string]: string}[]) {
        const reader = new FileReaderSync();
        const data = reader.readAsBinaryString(file);
        const workbook = xlsx.read(data, {type: 'binary'});
        return callback(workbook)
    }
    
    readWorkbook(workbook: xlsx.WorkBook) {
        const sheetNames = workbook.SheetNames; // 
        const worksheet = workbook.Sheets[sheetNames[0]]; // sheet
        const targettable = xlsx.utils.sheet_to_json(worksheet) as {[key: string]: string}[];
        return targettable;
    }

    /**
     * read barcode table and save information in json style
     * @param contigs merged haplotype
     * @param thresholdp minimum percentage of haplotype count reserved
     * @param thresholdn minimum count of haplotype reserved
     * @returns {[key: string]: {[key: string]: number}}
     */
    splitByBarcode(contigs: {[key: string]: number}, thresholdp = 0, thresholdn = 0) {
        //todo: samplebarcode-
        const res: any = {}
        for (const sample of this.table) {
            const barcodelen = sample['Barcode_R'].length
            res[sample['Sample']] = {}
            let sumup = 0
            for (const read in contigs) {
                if (read.slice(0, barcodelen) == sample['Barcode_R'] && read.slice(-barcodelen) == this.revcomp(sample['Barcode_L'])) {
                    res[sample['Sample']][this.revcomp(read)] = contigs[read]
                    sumup += contigs[read]
                } else if (read.slice(0, barcodelen) == sample['Barcode_L'] && read.slice(-barcodelen) == this.revcomp(sample['Barcode_R'])) {
                    res[sample['Sample']][read] = contigs[read]
                    sumup += contigs[read]
                }
            }
            console.log('sumup:', sample['Sample'], sumup)
            for (const read in res[sample['Sample']]) {
                if (res[sample['Sample']][read] < sumup * thresholdp || res[sample['Sample']][read] < thresholdn) delete res[sample['Sample']][read]
            }
        }
        this.sampletable = res
        return res
    }

    /**
     * 
     * @param samplebybarcodes 
     * @returns {[key: string]: {[key: string]: {[key: string]: string | {[key: string]: number}}}}
     */
    splitByGene(samplebybarcodes: {[key: string]: {[key: string]: number}}) {
        const res: any = {}
        for (const sample of this.table) {
            if (!(sample['gene_name'] in res)) res[sample['gene_name']] = {}
            for (const samplebb in samplebybarcodes) {
                if (sample['Sample'] == samplebb) {
                    if (!(samplebb in res[sample['gene_name']])) res[sample['gene_name']][samplebb] = {}
                    res[sample['gene_name']][samplebb]['seq'] = samplebybarcodes[samplebb]
                    res[sample['gene_name']][samplebb]['gRNA_PAM'] = sample['gRNA_PAM'].toUpperCase()
                    // res[sample['gene_name']][samplebb]['vector'] = sample['Vector']
                    // res[sample['gene_name']][samplebb]['note'] = sample['Note']
                }
            }
        }
        this.gene_sample_table = res
        return res
    }

    revcomp(seq: string) {
        function complement(a: string) {
            return ({ A: 'T', T: 'A', G: 'C', C: 'G' , R: 'Y', Y: 'R', a: 't', t: 'a', c: 'g', g: 'c',r: 'y', y: 'r'} as { [key:string]: string })[a]
        }
        return seq.split('').reverse().map(complement).join('')
    }
}

class Sample {
    name: string
    gene: string
    // vector: string
    // note: string
    gRNA_PAM: string
    barcode_L: string
    barcode_R: string
    group: string
    sumup: number | undefined
    ref: string | undefined
    reverse: boolean | undefined
    identification: string | undefined
    constructor(sample = '', gene = '', gRNA_PAM = '', barcode_L = '', barcode_R = '', group = '', identification?: string) {
        this.name = sample
        this.gene = gene
        // this.vector = vector
        // this.note = note
        this.gRNA_PAM = gRNA_PAM
        this.barcode_L = barcode_L
        this.barcode_R = barcode_R
        this.group = group
        if (identification) this.identification = identification
    }

    /**
     * get sum of haplotype reads count
     * @param haplotypes splited haplotypes of sample
     */
    sumUp(haplotypes: {[key: string]: number}) {
        let sum = 0
        for (const haplotype in haplotypes) {
            sum += haplotypes[haplotype]
        }
        this.sumup = sum
    }

    /**
     * get reference sequence at gRNA of sample
     * @param geneseq complete gene sequence
     * @param gRNA_PAM gRNA sequence
     */
    getRef(geneseq: string) {
        let ref = geneseq
        let rev = false
        if (ref.indexOf(this.gRNA_PAM) == -1) {
            ref = revcomp(geneseq)
            rev = true
        }
        try {
            const gRNA_PAM_pos = ref.indexOf(this.gRNA_PAM)
            this.ref = ref.slice(gRNA_PAM_pos > 70 ? gRNA_PAM_pos - 70 : 0, gRNA_PAM_pos + 70 + this.gRNA_PAM.length)
            this.reverse = rev
        } catch (error) {
            console.error(error)//todo: notify error report
        }
    }
}

function revcomp(seq: string) {
    function complement(a: string) {
        return ({ A: 'T', T: 'A', G: 'C', C: 'G' , R: 'Y', Y: 'R', a: 't', t: 'a', c: 'g', g: 'c',r: 'y', y: 'r'} as { [key:string]: string })[a]
    }
    return seq.split('').reverse().map(complement).join('')
}

/**
 * split merged data by barcode
 * @param table sample information table
 * @param haplotype merged haplotypes
 * @returns {sample: Sample, haplotype: {[key: string]: number}}[]
 */
function splitByBarcode(table:{[key: string]: string}[], haplotype: {[key: string]: number}) {
    const res: {sample: Sample, haplotype: {[key: string]: number}}[] = []
    for (const line of table) {
        let sample: Sample
        let haplotypes: {[key: string]: number} = {}
        if (line.identification && line.identification.length > 0) {
            sample = new Sample(line.name, line.gene, line.gRNA_PAM.toUpperCase(), line.barcode_L, line.barcode_R, line.group, line.identification)
            haplotypes = splitByBarcodeIdentification(sample, haplotype)
        } else {
            sample = new Sample(line.name, line.gene, line.gRNA_PAM.toUpperCase(), line.barcode_L, line.barcode_R, line.group)
            haplotypes = splitByBarelyBarcode(sample, haplotype)
        }
        res.push({sample: sample, haplotype: haplotypes})
    }
    return res
}

function splitByBarelyBarcode(sample: Sample, haplotype: {[key: string]: number}, thresholdn = 0, thresholdp = 0) {
    const barcodelen = sample.barcode_L.length
    const ha_in_sample: {[key: string]: number} = {}
    for (const read in haplotype) {
        if ((read.slice(0, barcodelen) == sample.barcode_R && read.slice(-barcodelen) == revcomp(sample.barcode_L))) {
            if (ha_in_sample[revcomp(read)]) {
                ha_in_sample[revcomp(read)] += haplotype[read]
            } else {
                ha_in_sample[revcomp(read)] = haplotype[read]
            }
        } else if (read.slice(0, barcodelen) == sample.barcode_L && read.slice(-barcodelen) == revcomp(sample.barcode_R)) {
            if (ha_in_sample[read]) {
                ha_in_sample[read] += haplotype[read]
            } else {
                ha_in_sample[read] = haplotype[read]
            }
        }
    }
    sample.sumUp(ha_in_sample)
    if (thresholdn !== 0 || thresholdp !== 0) {
        for (const ha in ha_in_sample) {
            if (ha_in_sample[ha] < thresholdn || ha_in_sample[ha] < (sample.sumup as number) * thresholdp) {
                delete ha_in_sample[ha]
            }
        }
    }
    return ha_in_sample
}

function splitByBarcodeIdentification(sample: Sample, haplotype: {[key: string]: number}) {
    const barcodelen = sample.barcode_L.length
    const ha_in_sample: {[key: string]: number} = {}
    for (const read in haplotype) {
        if (read.slice(0, barcodelen) == sample.barcode_R && read.slice(-barcodelen) == revcomp(sample.barcode_L) && read.slice(barcodelen).endsWith(revcomp(sample.identification as string))) {
            if (ha_in_sample[revcomp(read)]) {
                ha_in_sample[revcomp(read)] += haplotype[read]
            } else {
                ha_in_sample[revcomp(read)] = haplotype[read]
            }
        } else if (read.slice(0, barcodelen) == sample.barcode_L && read.slice(-barcodelen) == revcomp(sample.barcode_R) && read.slice(barcodelen).startsWith(sample.identification as string)) {
            if (ha_in_sample[read]) {
                ha_in_sample[read] += haplotype[read]
            } else {
                ha_in_sample[read] = haplotype[read]
            }
        }
    }
    sample.sumUp(ha_in_sample)
    return ha_in_sample
}


function table_into_sampletable(...tables: {[key: string]: string}[][]) {
    const sampletable: Sample[] = []
    for (const table of tables) {
        for (const line of table) {
            const newline = new Sample()
            for (const ele in line) {
                switch(ele) {
                    case 'Gene':
                        newline.gene = line[ele]
                        break
                    // case 'Vector':
                    //     newline.vector = line[ele]
                    //     break
                    // case 'Note':
                    //     newline.note = line[ele]
                    //     break
                    case 'gRNA_PAM':
                        newline.gRNA_PAM = line[ele]
                        break
                    case 'Barcode_L':
                        newline.barcode_L = line[ele]
                        break
                    case 'Barcode_R':
                        newline.barcode_R = line[ele]
                        break
                    case 'Sample':
                        newline.name = line[ele]
                        break
                    case 'Group':
                        newline.group = line[ele]
                }
            }
            sampletable.push(newline)
        }
    }
    return sampletable
}

/**
 * split samples by group
 * @param samples 
 * @returns {sample: Sample, data: any}[][]
 */
function splitByGroup(samples: {sample: Sample, haplotype: any}[]) {
    return samples.reduce((memo, x) => {
        for (const group of memo) {
            if (group[0].sample.group == x.sample.group) {
                group.push(x)
                return memo
            }
        }
        memo.push([x])
        return memo
    }, [] as {sample: Sample, haplotype: any}[][])
}

export {
    zip,
    azip,
    Table,
    Sample,
    splitByBarcode,
    splitByGroup,
    table_into_sampletable
}